import inventoryParts from './inventory_parts.json'

type InventoryPart = {
  colorId: string,
  isSpare: boolean,
  partNum: string
  quantity: number
}

export default inventoryParts.reduce((acc: { [key: string]: InventoryPart[]; }, part: any) => {
    acc[part.i] = acc[part.i] || []
    acc[part.i].push({
      partNum: part.n,
      colorId: part.c,
      quantity: part.q,
      isSpare: part.s == 't'
    })
    return acc
  }, {})